import { SafeHtml } from "@angular/platform-browser";
import { EditorApiService } from "./editor.api.service";
import { Const } from "./shared/constants";


export class EditorApi {

  constructor(private service: EditorApiService) {}

  public clearFormatting(){
    this.service.clearFormattingSubject.next();
  }

  public triggerBold(){
    this.service.boldSubject.next();
  }
  
  public triggerItalic(){
    this.service.italicSubject.next();
  }

  public triggerUnderline(){
    this.service.underlineSubject.next();
  }

  public triggerStrikethrough(){
    this.service.strikethroughSubject.next();
  }

  public triggerSubscript(){
    this.service.subscriptSubject.next();
  }

  public triggerSuperscript(){
    this.service.superscriptSubject.next();
  }

  public triggerMention(){
    this.service.mentionSubject.next();
  }

  public setTextStyle(style: 'p' | 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6'){
    this.service.textStyleSubject.next(style);
  }

  public setTextColor(color: 'Black' | 'White' | 'Red' | 'Green' | 'Blue' |
  'Yellow' | 'Cyan' | 'Magenta' | 'Silver' | 'Gray' | 'Maroon' | 'Olive' |
  'Purple' | 'Teal' | 'Navy' | 'Coral' | 'Turquoise' | 'Salmon' |
  'Lime' | 'Gold' | 'Orchid'){
    const item = Const.colors.find(col => col.colorName === color);
    if(item){
      this.service.textColorSubject.next(item);
    }
  }

  public setInnerHTML(html: string | SafeHtml){
    this.service.innerHTMLSubject.next(html);
  }
    
  public triggerBulletList(){
    this.service.bulletListSubject.next();
  }

  public triggerNumberedList(){
    this.service.numberedListSubject.next();
  }

  
}
