import { Injectable } from '@angular/core';
import { SafeHtml } from '@angular/platform-browser';
import { Subject } from 'rxjs';
import { Color } from './components/text-color/text-color.component';

@Injectable()
export class EditorApiService {

  clearFormattingSubject = new Subject<void>();
  boldSubject = new Subject<void>();
  italicSubject = new Subject<void>();
  underlineSubject = new Subject<void>();
  strikethroughSubject = new Subject<void>();
  subscriptSubject = new Subject<void>();
  superscriptSubject = new Subject<void>();
  mentionSubject = new Subject<void>();
  textStyleSubject = new Subject<string>();
  textColorSubject = new Subject<Color>();
  innerHTMLSubject = new Subject<string | SafeHtml>();
  bulletListSubject = new Subject<void>();
  numberedListSubject = new Subject<void>();

  clearFormatting$ = this.clearFormattingSubject.asObservable();
  bold$ = this.boldSubject.asObservable();
  italic$ = this.italicSubject.asObservable();
  underline$ = this.underlineSubject.asObservable();
  strikethrough$ = this.strikethroughSubject.asObservable();
  subscript$ = this.subscriptSubject.asObservable();
  superscript$ = this.superscriptSubject.asObservable();
  mention$ = this.mentionSubject.asObservable();
  textStyle$ = this.textStyleSubject.asObservable();
  textColor$ = this.textColorSubject.asObservable();
  innerHTML$ = this.innerHTMLSubject.asObservable();
  bulletList$ = this.bulletListSubject.asObservable();
  numberedList$ = this.numberedListSubject.asObservable();
}
